.contact {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  background-color: rgb(246, 216, 255);

  .contact-list-container {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;

    .contact-list {
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
      padding: 5%;
      height: 30vh;
      width: 40vw;
      background-color: whitesmoke;
      border-radius: 0.25rem;

      .contact-row {
        display: flex;
        align-items: center;
        text-align: center;
        line-height: 2%;

        .contact-icon {
          flex: 1;
          font-size: 1.5rem;
        }

        .contact-info {
          flex: 2;
          text-align: justify;
        }
      }
    }
  }
}
